/* big button */
.wpo_primary_big {
	padding: 4px 6px !important;
	font-size:22px !important;
	min-height: 34px;
	min-width: 200px;
}

/*  SECTIONS  */
.wpo_section {
	clear: both;
	padding: 0;
	margin: 0;
}

.wp-optimize-settings {
	margin-bottom: 16px;
}

/*  COLUMN SETUP  */
.wpo_col {
	display: block;
	float: left;
	margin: 1% 0 1% 1%;
}
.wpo_col:first-child {
	margin-left: 0;
}

/*  GROUPING  */
.wpo_group:before,
.wpo_group:after {
	content: "";
	display: table;
}
.wpo_group:after {
	clear: both;
}

.wpo_half_width {
	width: 48%;
}

/*  GRID OF THREE  */
.wpo_span_3_of_3 {
	width: 100%;
}
.wpo_span_2_of_3 {
	width: 65.3%;
}
.wpo_span_1_of_3 {
	width: 32.1%;
}

.nav-tab-wrapper {
	margin: 14px 0px;
}

@media screen and (min-width: 549px) {
	.show_on_default_sizes {
		display: block !important;
	}
	.show_on_mobile_sizes {
		display: none !important;
	}
}

@media screen and (max-width: 548px) {
	.show_on_default_sizes {
		display: none !important;
	}
	.show_on_mobile_sizes {
		display: block !important;
	}
}

@media screen and (max-width: 768px) {
	.wpo_col {
		margin: 1% 0;
	}
	.wpo_span_3_of_3 {
		width: 100%;
	}
	.wpo_span_2_of_3 {
		width: 100%;
	}
	.wpo_span_1_of_3 {
		width: 100%;
	}
	.wpo_half_width {
		width: 100%;
	}
}

.wp-optimize-settings-clean-transient label, .wp-optimize-settings-clean-pingbacks label, .wp-optimize-settings-clean-trackbacks label, .wp-optimize-settings-clean-postmeta label, .wp-optimize-settings-clean-orphandata label, .wp-optimize-settings-clean-commentmeta label {
	color: #9B0000;
}

td.wp-optimize-settings-optimization-checkbox {
	width: 18px;
	padding-left: 4px;
	padding-right: 0px;
}
.wp-optimize-settings-optimization-checkbox input {
	margin: 0px;
	padding: 0px;
}

#retention-period {
	width: 60px;
}

.wp-optimize-settings-optimization-info {
	font-size: 80%;
	padding-left: 32px;
	font-style: italic;
}

.wp-optimize-settings input[type="checkbox"] {
	width: 18px;
}

/* Added for the Image on Addons tab*/
img.addons {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 260px;
  height: 220px;
}

.wpo_spinner {
	width: 18px;
	height: 18px;
	padding-left: 10px;
	display: none;
	position: relative;
	top: 4px;
}

#wp-optimize-auto-options {
	margin-left: 28px;
}

.display-none {
	display: none;
}

#save_done {
	color: #d94f00;
	font-size: 250%;
}

.wp-optimize-settings-optimization-run-spinner {
	position: relative;
	top: 2px;
}

#wp_optimize_table_list_refresh {
	float: right;
}
#wp_optimize_table_list_refresh:hover {
	cursor: pointer;
}
